import React from 'react';
import {
	ActionsButtons,
	ConfirmModal,
	ErrorBoundary,
	Loader,
	StyledTable,
	StyledTableCell,
	StyledTableHeadCell,
	StyledTableRow
} from 'shared/components';
import { useAppState } from 'shared/state';
import EntityContainer from 'modules/entity/containers';
import {
	Pagination,
	TableBody,
	TableContainer,
	TableHead,
	Tooltip
} from 'shared/components/Elements';
import { usePagination, useRouter } from 'shared/hooks';
import { useClientsManagementContext } from '../../context/ClientsManagementContext';
import { ClientCompany } from '../../../../shared/typings/client.types';
import { ClientName } from '../../components';
import { getClientName, getClientType } from '../../utils';
import get from 'lodash/get';
import dayjs from 'dayjs';
import { useClientList } from '../../hooks';
import { useDebounce } from 'use-debounce';
import { Link } from 'react-router-dom';

function ClientsListInner() {
	const { companyId, isPermitted } = useAppState();
	const { page, handlePage } = usePagination();
	const { searchQuery: query } = useClientsManagementContext();
	const { match, history } = useRouter();
	const { deleteLoading, handleClientDelete, showDeleteVisible, closeDeletePopup, deleteVisible } =
		useClientList();
	const [searchQuery] = useDebounce<string>(query, 500);

	const deleteClientHandler = () => () => handleClientDelete(deleteVisible!);

	return (
		<div>
			{companyId ? (
				<EntityContainer.All
					entity="clients"
					name="AllClients"
					url={`/${companyId}/client_companies/`}
					params={{
						limit: 10,
						extra: {
							search: searchQuery
						},
						page
					}}
					replaceIds
				>
					{({ items, isFetched, meta }) => {
						const clients = items as ClientCompany[];
						return (
							<div>
								<TableContainer classes={{ root: 'relative ' }}>
									<StyledTable
										aria-label="simple table"
										classes={{ root: 'table-fixed dark-mode-styled-table' }}
									>
										<TableHead>
											<StyledTableRow>
												<StyledTableHeadCell style={{ width: '20%' }}>
													Client Name
												</StyledTableHeadCell>
												<StyledTableHeadCell style={{ width: '10%' }}>
													Client Type
												</StyledTableHeadCell>
												{/*<StyledTableHeadCell style={{ width: '7%' }}>Users</StyledTableHeadCell>*/}
												<StyledTableHeadCell style={{ width: '7%' }}>Cases</StyledTableHeadCell>
												<StyledTableHeadCell style={{ width: '10%' }}>Status</StyledTableHeadCell>
												<StyledTableHeadCell style={{ width: '20%' }}>
													Registered
												</StyledTableHeadCell>
												<StyledTableHeadCell style={{ width: '15%' }}>
													Uploaded File Name
												</StyledTableHeadCell>
												<StyledTableHeadCell style={{ width: '11%', textAlign: 'center' }}>
													Actions
												</StyledTableHeadCell>
											</StyledTableRow>
										</TableHead>
										<TableBody>
											{clients.map((item) => {
												const {
													is_individual,
													icon,
													owner,
													name,
													cases_count_as_client,
													status,
													imported_from_file,
													id
												} = item;
												return (
													<StyledTableRow key={id}>
														<StyledTableCell>
															{item.owner ? (
																<Link to={`${match.path}${item.id}`}>
																	<ClientName
																		name={name}
																		icon={icon}
																		is_individual={is_individual}
																		owner={owner}
																		nameClassNames="text-primary transition hover:text-primary-dark"
																	/>
																</Link>
															) : (
																<Tooltip title="Something wrong with this client" placement="top">
																	<div>
																		<ClientName
																			name={name}
																			icon={icon}
																			is_individual={is_individual}
																			owner={owner}
																		/>
																	</div>
																</Tooltip>
															)}
														</StyledTableCell>
														<StyledTableCell>
															<strong>{getClientType(is_individual)}</strong>
														</StyledTableCell>
														<StyledTableCell>{cases_count_as_client ?? 0}</StyledTableCell>
														{/*<StyledTableCell>{users_count}</StyledTableCell>*/}
														<StyledTableCell>
															<div className="h-5 rounded-full bg-primary text-center text-xs font-medium leading-5 text-white">
																{status}
															</div>
														</StyledTableCell>
														<StyledTableCell>
															{dayjs(get(item, 'created_date', '')).format('MMMM D YYYY, h:mm a')}
														</StyledTableCell>
														<StyledTableCell>
															<div className=" truncate">{imported_from_file}</div>
														</StyledTableCell>
														<StyledTableCell>
															<div className="flex items-center justify-center">
																<ActionsButtons.DeleteButton
																	isDisabled={!isPermitted}
																	clickHandler={() => showDeleteVisible(item)}
																/>
																<ActionsButtons.ViewButton
																	clickHandler={() => history.push(`${match.path}${item.id}`)}
																	isDisabled={!item.owner}
																/>
																{/*<ActionsButtons.EditButton*/}
																{/*	isDisabled={!isPermitted}*/}
																{/*	clickHandler={() => showEditVisible(item)}*/}
																{/*/>*/}
															</div>
														</StyledTableCell>
													</StyledTableRow>
												);
											})}
										</TableBody>
									</StyledTable>
									{!isFetched && <Loader tiled withOverlay />}
								</TableContainer>
								<ConfirmModal
									featureHandler={deleteClientHandler()}
									isOpen={Boolean(deleteVisible)}
									toggleHandler={closeDeletePopup}
									title={`Are You Sure To Delete Client ${getClientName(
										(deleteVisible as ClientCompany)?.is_individual,
										(deleteVisible as ClientCompany)?.name,
										(deleteVisible as ClientCompany)?.owner
									)}?`}
									hasLoader
									isLoading={deleteLoading}
								/>
								{/*<WhiteModal handleModalOpen={closeEditPopup} open={Boolean(editVisible)}>*/}
								{/*	<EditInvitation entity={editVisible!} onSuccess={closeEditPopup} />*/}
								{/*</WhiteModal>*/}
								<div className="news__pagination my-4">
									{items.length ? (
										<Pagination
											count={meta?.last_page}
											page={page ? +page : 1}
											defaultPage={6}
											siblingCount={1}
											color={'primary'}
											onChange={handlePage}
										/>
									) : null}
								</div>
							</div>
						);
					}}
				</EntityContainer.All>
			) : (
				<div className="flex justify-center">
					<Loader />
				</div>
			)}
		</div>
	);
}

export default function ClientsList() {
	return (
		<ErrorBoundary>
			<ClientsListInner />
		</ErrorBoundary>
	);
}
